html,
body {
  margin: 0;
  padding: 0;
}
html {
  height: 100%;
  font-size: 14px;
}
body {
  width: 100%;
  height: 100%;
  min-height: 100%;
  background: #e7e8eb;
}
header {
  width: 100%;
  background-color: #fff;
  border-bottom: 1px solid #d9dadc;
}
.container {
  width: 1024px;
  margin: 0 auto;
}
.banner {
  color: #222;
  position: relative;
  text-align: center;
}
.banner.cover {
  background: url(../images/bg.jpg) no-repeat top center;
  background-size: cover;
  min-height: 660px;
}
.banner.cover::before {
  content: "";
  display: block;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  position: absolute;
  background: #a83279;
  background-repeat: repeat-x;
  background-image: -webkit-linear-gradient(-45deg, #a83279, #d38312);
  background-image: linear-gradient(-45deg, #a83279, #d38312);
  opacity: .8;
  filter: alpha(opacity=80);
}
.main-nav {
  width: 100%;
  border-bottom: 1px solid rgba(255, 255, 255, 0.15);
  position: relative;
}
ul {
  margin: 0;
}
.main-nav-menu li {
  display: inline-block;
  font-size: 14px;
}
.main-nav-menu li > a {
  color: #222;
  display: block;
  padding: 10px 15px;
  line-height: 30px;
}
.main-nav-menu li > a:hover {
  text-decoration: none;
  background: 0;
  color: #222;
}
.main-nav-menu li.active {
  margin-bottom: -1px;
  border-bottom: 1px solid #fff;
}
.main-nav-menu li.active > a {
  color: #222;
}
/*
 * editor
 */
#blog-editor {
  height: 100%;
}
.editor-toolbar::after {
  content: '';
  display: block;
  visibility: hidden;
  clear: both;
}
.post-options > div {
  display: inline-block;
}
.post-tags .tags-storing {
  display: inline-block;
}
.post-tags .tags-inputer {
  display: inline-block;
}
.post-action {
  float: right;
}
.post-content,
.preview-content {
  box-sizing: border-box;
  height: 500px;
}
#post {
  margin-top: 30px;
  margin-bottom: 50px;
  border: 1px solid #d9dadc;
  background-color: #fff;
}
#post article {
  padding: 1em;
  margin-bottom: 1em;
  box-sizing: border-box;
  background: #fff;
}
#post article > h3 {
  color: #222;
}
#post article + article {
  border-top: 1px solid #EFF0F1;
}
.entry {
  padding: 1em;
  margin-top: 30px;
  margin-bottom: 50px;
  border: 1px solid #d9dadc;
  background-color: #fff;
}
.marknote {
  background: #F8F2E4;
}
.marknote.preview .area-writing {
  width: 100%;
}
.marknote.writing .area-writing {
  margin: 0 auto;
}
.marknote .area-writing {
  height: 100%;
}
.marknote .CodeMirror {
  height: 100%;
  padding: 2em 30%;
  background: #F8F2E4;
}



